import { useState } from 'react';
import { Select } from './components/Select';
import { Stack } from '@mui/material';

export default function Home() {
  const [value, setValue] = useState<number | null>(null);

  return (
    <Stack>
      <Select
        value={value}
        options={[
          { id: 1, label: "One" },
          { id: 2, label: "Two" },
        ]}
        onChange={v => setValue(v as number)}
      />
    </Stack>
  );
}
