import { useState, useRef } from 'react';
import { Button, Popper, Backdrop, MenuList } from "@mui/material"

import type { Value } from "./types";
import Option from "./Option";

function Select({
    options,
    filterable,
    value,
    onChange,
}: {
    options: any[];
    filterable?: boolean;
    value: Value;
    onChange: (value: Value) => void;
}) {
    const [isOpen, setIsOpen] = useState(false);
    const buttonRef = useRef<HTMLButtonElement | null>(null);

    const handleOptionCLick = (id: string | number) => {};

    return (
        <>
            <Button ref={buttonRef} variant='outlined' onClick={() => setIsOpen(true)}></Button>
            <Backdrop open={isOpen} onClick={() => setIsOpen(false)}>
                <Popper
                    anchorEl={buttonRef?.current}
                    open={isOpen}
                    placement='bottom-start'
                >
                    <MenuList>
                        {options.map(option => (
                            <Option
                                option={option}
                                onClick={handleOptionCLick}
                                value={value}
                            />
                        ))}
                    </MenuList>
                </Popper>
            </Backdrop>
        </>
    )
};

export default Select;